@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.hero {
  height: 100%;
  width: 100%;
  position: absolute;
  background-image: url(/FullStack_Blog_App_FrontEnd/static/media/norway.09b97a96.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.title-container {
  width: 380px;
  margin-top: 4rem;
  margin-bottom: 1.5rem;
  background-color: rgba(63, 182, 212, 0.4);
  box-shadow: 0 0 18px #333;
  text-align: center;
  padding: 10px;
  border-radius: 20px;
  border: 1px solid #fbc531;
}

.title {
  color: #fff;
  font-size: 2.3rem;
  font-family: "Cinzel", serif;
}

.form-box {
  width: 380px;
  height: 500px;
  position: relative;
  background-color: rgba(63, 182, 212, 0.4);
  box-shadow: 0 0 18px #333;
  padding: 5px;
  border-radius: 20px;
  border: 1px solid #fbc531;
}
.button-box {
  display: flex;
  justify-content: space-between;
  width: 220px;
  margin: 20px auto;
  position: relative;
  border-radius: 30px;

  background: linear-gradient(to right, #2c0116, #037cec, #2c0116);
}
.button-box:hover {
  box-shadow: 2px 2px 5px #333;
}
.toggle-btn {
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 10px 30px;
  cursor: pointer;
  background: transparent;
  border: 0;
  outline: none;
  position: relative;
}

.float-btn {
  position: absolute;
  top: 0;
  left: 0;
  width: 110px;
  height: 100%;
  background: linear-gradient(to right, #25afda, #2c0116, #25afda);
  background: #25afda;
  border-radius: 40px;
  border: 2.5px solid #fff;
  transition: 0.5s;
}

.continue {
  margin-top: 10px;
  cursor: pointer;
  color: yellow;
  font-weight: bold;
}

@media (max-width: 767px) {
  .title {
    font-size: 2rem;
  }
}

@media (max-width: 450px) {
  .title {
    font-size: 1.6rem;
  }
  .title-container {
    width: 85vw;
  }
  .form-box {
    width: 85vw;
    height: 70vh;
  }
}

.pagination {
  margin: 15px auto;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination > .active > a {
  background-color: #351f39;
  border-color: #351f39;
  color: #fff;
}
.pagination > li > a {
  border: 1px solid #351f39;
  border-radius: 18px;
  padding: 5px 10px;
  margin: 8px;
  outline: none;
  cursor: pointer;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  background-color: #351f39;
  border-color: #351f39;
  outline: none;
}
.pagination > li > a,
.pagination > li > span {
  color: #351f39;
  cursor: pointer;
  transition: 0.5s;
}
.pagination > li > a:hover,
.pagination > li > span:hover {
  box-shadow: 0 0 3px #351f39, 0 0 7px #351f39;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  font-weight: bold;
  border: 1px solid;
  cursor: pointer;
}

.flyingButton {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  position: fixed;
  top: 90px;
  right: 50px;
  background-color: tomato;
  color: #fff;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
  line-height: 5rem;
  box-shadow: 3px 3px 6px #837777;
}

.createPostContainer {
  position: absolute;
  width: 8rem;
  height: 3.4rem;
  line-height: 3.4rem;
  border-radius: 1.2rem;
  top: 6rem;
  left: 8rem;
  font-size: 1rem;
  background-color: #383e56;
  color: #fb743e;
  box-shadow: 3px 3px 6px #837777;
  transition: all 0.6s ease-in-out;
}

.flyingButton:hover {
  cursor: pointer;
  background-color: rgb(245, 48, 13);
}
.flyingButton:active {
  top: 92px;
}

.flyingButton:hover .createPostContainer {
  left: -1.5rem;
}

@media (max-width: 500px) {
  .flyingButton {
    width: 3rem;
    height: 3rem;
    top: 110px;
    right: 20px;
    font-size: 3rem;
    line-height: 3rem;
  }

  .createPostContainer {
    visibility: hidden;
  }

  .flyingButton:hover {
    cursor: pointer;
    background-color: rgb(245, 48, 13);
  }

  .flyingButton:active {
    top: 112px;
  }
}

table {
  border-collapse: collapse;
}
table td {
  font-weight: bold;
  padding: 8px;
}

table tr {
  border: 1px solid #ddd;
}

table tr:nth-child(even) {
  background-color: #b4ccf5;
}

.btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

textarea:hover {
  background-color: beige;
  box-shadow: 2px 2px 5px #555;
}

